import { ColumnDef, Row } from '@tanstack/react-table';
import { useNavigate } from 'react-router-dom';
import { Message } from '@/types/message.types';
import AddIcon from '@/components/icons/AddIcon';
import MessageIcon from '@/components/icons/messages/MessageIcon';

// Create a separate component for the actions cell
const ActionsCell = ({ row }: { row: Row<Message> }) => {
    const navigate = useNavigate();

    return (
        <div className="me-6 text-end flex flex-row items-center justify-end gap-7">
            <button
                onClick={() => navigate(`/customer-relations/customer-communication/add-message/${row.original.id}`)}
                className="hover:bg-white-blue-300 rounded-full p-1"
            >
                <AddIcon className="text-green-success-600" />
            </button>
            <button
                // onClick={() => navigate(`/customer-relations/view-customer/${row.original.id}`)}
                className="hover:bg-white-blue-300 rounded-full p-1"
            >
                <MessageIcon className="text-primery-blue-500" />
            </button>
        </div>
    );
};

export const columns: ColumnDef<Message>[] = [
    {
        accessorKey: 'id',
        header: () => {
            return (
                <div className="text-sm px-4 py-3 !text-primery-blue-500 leading-[26.24px] font-semibold tracking-normal">
                    ID
                </div>
            );
        },
        cell: ({ row }) => (
            <div className="text-sm px-4 py-3 !text-primery-blue-500 leading-[26.24px] font-semibold tracking-normal">
                {row.original.id}
            </div>
        ),
    },
    {
        accessorKey: 'name',
        header: () => {
            return (
                <div className="text-sm px-4 py-3 !text-primery-blue-500 leading-[26.24px] font-semibold tracking-normal">
                    اسم العميل
                </div>
            );
        },
        cell: ({ row }) => (
            <div className="text-sm px-4 py-3 !text-primery-blue-500 leading-[26.24px] font-semibold tracking-normal">
                {row.original.full_name}
            </div>
        ),
    },
    {
        accessorKey: 'status',
        header: () => {
            return (
                <div className="text-sm px-4 py-3 !text-primery-blue-500 leading-[26.24px] font-semibold tracking-normal">
                    رقم الهاتف
                </div>
            );
        },
        cell: ({ row }) => {
            return (
                <div className="text-sm text-[#625E74] font-normal tracking-normal leading-[26.4px]">
                    {row.original.phone}
                </div>
            );
        },
    },
    {
        accessorKey: 'date',
        header: () => {
            return (
                <div className="text-sm px-4 py-3 !text-primery-blue-500 leading-[26.24px] font-semibold tracking-normal">
                    عدد الرسائل المرسلة
                </div>
            );
        },
        cell: ({ row }) => {
            return (
                <div className="text-sm text-[#625E74] font-normal tracking-normal leading-[26.4px]">
                    {row.original.sms_count + row.original.email_count}
                </div>
            );
        },
    },
    {
        accessorKey: 'service',
        header: () => {
            return (
                <div className="text-sm px-4 py-3 !text-primery-blue-500 leading-[26.24px] font-semibold tracking-normal">
                    عدد الرسائل النصية
                </div>
            );
        },
        cell: ({ row }) => {
            return (
                <div className="text-sm text-[#625E74] font-normal tracking-normal leading-[26.4px]">
                    {row.original.sms_count}
                </div>
            );
        },
    },
    {
        accessorKey: 'location',
        header: () => {
            return (
                <div className="text-sm px-4 py-3 !text-primery-blue-500 leading-[26.24px] font-semibold tracking-normal">
                    عدد رسائل الإيميل
                </div>
            );
        },
        cell: ({ row }) => {
            return (
                <div className="text-sm text-[#625E74] font-normal tracking-normal leading-[26.4px]">
                    {row.original.email_count}
                </div>
            );
        },
    },
    {
        id: 'actions',
        header: () => {
            return (
                <div className="text-sm text-center px-4 py-3 !text-primery-blue-500 leading-[26.24px] font-semibold tracking-normal">
                    للاطلاع على الرسائل
                </div>
            );
        },
        cell: ({ row }) => <ActionsCell row={row} />,
    },
];
