import { Card } from '@/types/statistics';
import StatisticsCards from '@/components/cards/StatisticsCards';
import SettingsIcon from '@/components/icons/statistics/SettingIcon';
import UserAddIcon from '@/components/icons/statistics/UserAddIcon';
import MessagesIcon from '@/components/icons/statistics/MessagesIcon';
import { useEffect, useState } from 'react';
import { apiGet } from '@/lib/axios/axios-utils';
import { endpoints } from '@/lib/axios/axios-global';
import { CustomerCommunicationStats } from '../../types/customerCommunicationStats.types';

export default function CommunicationStatisticsCards() {
    const [stats, setStats] = useState<CustomerCommunicationStats | null>(null);

    useEffect(() => {
        const fetchStats = async () => {
            try {
                const res = await apiGet({ endpoint: endpoints.cutomerContact.dashboard });
                setStats(res.data.data);
            } catch (err) {
                setStats(null);
            }
        };
        fetchStats();
    }, []);

    const cards: Card[] = [
        { text: 'عدد الرسائل المرسله', icon: <MessagesIcon />, num: stats?.total, percent: 0 },
        { text: 'رسائل الإيميل', icon: <UserAddIcon />, num: stats?.msg_email, percent: 0 },
        { text: 'رسائل نصية SMS', icon: <SettingsIcon />, num: stats?.msg_sms, percent: 0 },
    ];

    return <StatisticsCards cards={cards} />;
}
